import { mapState, mapGetters } from 'vuex';
export default {
	data() {
		return {
			is_unfold: false,
			project_list: [],
			downOption: {
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption: {
				auto: false,
				isLock: true,
				use: false
			},
			swiperHeight: '',
			curent_p_index: 0,
			current_radio: 'can',
			has_reply: false,
			person_consol:{},
			tab_list: [
				{
					name: '进行中的任务'
				},
				{
					name: '已完成的任务'
				}
			],
			TabCur: 0,
		};
	},
	components: {},
	computed: {
		...mapState(['artisan_work_state']),
		...mapGetters(['user_role', 'id']),
		c_CustomBar() {
			return this.CustomBar;
		},
		c_project() {
			return this.project_list[this.curent_p_index];
		},
		finshTask() {
			let ft_list = this.$utils._get(this.person_consol, 'finshTask', []);
			ft_list.forEach(item =>{
				let list = item.userList.split(',')
				let plan_people = []
				list.forEach(val =>{
					let arr = val.split(':')
					let obj = {
						name:arr[2],
						avaurl:arr[0]+':'+arr[1]
					}
					plan_people.push(obj)
				})
				this.$set(item,'people',plan_people)
			})
			return ft_list
		},
		activeTask() {
			let at_list = this.$utils._get(this.person_consol, 'activeTask', [])
			at_list.forEach(item =>{
				let list = item.userList.split(',')
				let plan_people = []
				list.forEach(val =>{
					let arr = val.split(':')
					let obj = {
						name:arr[2],
						avaurl:arr[0]+':'+arr[1]
					}
					plan_people.push(obj)
				})
				this.$set(item,'people',plan_people)
			})
			return at_list
		},
		personHours() {
			return this.$utils._get(this.person_consol, 'personHours', {});
		},
		plan_list(){
			return this.TabCur === 0?this.activeTask:this.finshTask
		},
		no_task(){
			return this.plan_list.length === 0
		}
	},
	mounted() {
		uni.$on('refreshJwt',(data) =>{
			this.init()
		})
		this.init();
	},
	beforeDestroy() {
		uni.$off('refreshJwt')
	},
	methods: {
		init(cb) {
			this.$http
				.get('personwx.personproject/1.0/', {
					pid: this.id,
					isFinish: '0'
				})
				.then(res => {
					console.log(res);
					if (this.$utils._get(res, 'data.code', '') === '0') {
						this.project_list = this.$utils._get(res, 'data.data', []);
						if (this.project_list.length > 0) {
							if (this.c_project.resWorkStatus == '1') {
								this.$store.commit('setArtisanWorkState', 'waitStart');
							} else {
								this.$store.commit('setArtisanWorkState', 'confirmInfo');
							}
						} else {
							this.$store.commit('setArtisanWorkState', 'noTask');
						}
						if(this.artisan_work_state === 'confirmInfo'){
							this.get_person_consol()
						}
					}
				}).finally(() =>{
					if (typeof cb === 'function') {
						cb();
					}
					this.$nextTick(() => {
						this.get_swiper_c_height();
					});
				})
		},
		goto_work() {
			this.$http
				.post('personwx.changestatus/1.0/', {
					id: this.id,
					status: '0'
				})
				.then(res => {
					console.log(res);
					this.$store.commit('setArtisanWorkState', 'rest');
					uni.switchTab({
						url: '/pages/tabbar/task/task'
					});
				});
		},
		goto_rest() {
			this.$http
				.post('personwx.changestatus/1.0/', {
					id: this.id,
					status: '2'
				})
				.then(res => {
					console.log(res);
					this.$store.commit('setArtisanWorkState', 'noTask');
				});
		},
		exit_work() {},
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			// mescroll.resetUpScroll();
			setTimeout(() => {
				this.init(() => {
					mescroll.endSuccess();
				});
			}, 300);
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			//联网加载数据
			// setTimeout(() => {
			// 	this.init(() =>{
			// 		mescroll.endSuccess()
			// 	})
			// }, 300);
		},
		get_swiper_c_height() {
			const query = uni.createSelectorQuery().in(this);
			query
				.select('#sContent')
				.boundingClientRect(data => {
					console.log('得到布局位置信息' + JSON.stringify(data));
					console.log('节点离页面顶部的距离为' + data.top);
					this.swiperHeight = data.height + 'px';
				})
				.exec();
		},
		deliveryPeriod(time) {
			return this.$utils.format_date(time);
		},
		radioChange(e) {
			this.current_radio = e.target.value;
		},
		comfirm_can() {
			this.$http
				.post('personwx.personchangestatus/1.0/', {
					id: this.c_project.resWorkId,
					status: this.current_radio === 'can' ? '2' : '0'
				})
				.then(res => {
					console.log(res);
					this.has_reply = true;
					this.init();
				});
		},
		swiperChange(e) {
			console.log(e);
			this.curent_p_index = e.detail.current;
			if (this.c_project.resWorkStatus == '1') {
				this.$store.commit('setArtisanWorkState', 'waitStart');
			} else {
				this.$store.commit('setArtisanWorkState', 'confirmInfo');
			}
			this.$nextTick(() => {
				this.get_swiper_c_height();
				if(this.artisan_work_state === 'confirmInfo'){
					this.get_person_consol()
				}
			});
			// let id = this.$utils._get(this.bgt_c_task[this.current_swiper],'id','')
			// this.$store.commit('set_bgt_ct_id',id)
		},
		get_person_consol() {
			this.$http
				.get('personwx.personconsol/1.0/', {
					id: this.c_project.id,
					pid: this.id
				})
				.then(res => {
					console.log(res);
					if (res.data.code == 0) {
						this.person_consol = res.data.data;
					}
				});
				
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		}
	}
};
